___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
  margin: 0;
}

html {
  font-family:
    system-ui,
    -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    Roboto,
    Oxygen,
    Ubuntu,
    Cantarell,
    "Open Sans",
    "Helvetica Neue",
    sans-serif;
}

body {
  line-height: 1.5;
  background-color: var(--clr-bg-primary);
}

.wrapper {
  max-width: 1440px;
  margin: 0 auto;
  --header-height: 5rem;
}

header {
  height: var(--header-height);
}

main {
  height: calc(100vh - var(--header-height) - 4rem);
}

ul,
ol {
  list-style-type: none;
  padding: 0;
}

button {
  padding: 0.5em 2em;
  border: none;
  background-color: var(--clr-bg-accent);
  border-radius: 4px;
  box-shadow: 0 0 2px #111;
  cursor: pointer;
}

input,
select,
textarea {
  padding: 0.5em 1em;
}
